.App {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

.setup-screen {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.setup-container {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.1);
  max-width: 800px;
  text-align: center;
}

.setup-steps {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin: 30px 0;
}

.step {
  padding: 20px;
  background: #f8f9fa;
  border-radius: 12px;
  text-align: left;
}

.step h3 {
  margin: 0 0 10px 0;
  color: #333;
}

.privacy-notice {
  background: #e3f2fd;
  padding: 20px;
  border-radius: 8px;
  margin: 20px 0;
}

.setup-button {
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 25px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.2s;
}

.setup-button:hover {
  transform: translateY(-2px);
}

.app-header {
  background: rgba(255,255,255,0.1);
  backdrop-filter: blur(10px);
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.tracking-status {
  display: flex;
  align-items: center;
  gap: 10px;
}

.status-indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  animation: pulse 2s infinite;
}

.status-indicator.active {
  background: #4caf50;
}

.status-indicator.inactive {
  background: #f44336;
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

.tracking-toggle {
  background: rgba(255,255,255,0.2);
  color: white;
  border: 1px solid rgba(255,255,255,0.3);
  padding: 8px 16px;
  border-radius: 20px;
  cursor: pointer;
  transition: background 0.2s;
}

.tracking-toggle:hover {
  background: rgba(255,255,255,0.3);
}

.tab-navigation {
  display: flex;
  background: rgba(255,255,255,0.1);
  padding: 0 20px;
}

.tab-navigation button {
  background: none;
  border: none;
  color: rgba(255,255,255,0.7);
  padding: 15px 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s;
  border-bottom: 2px solid transparent;
}

.tab-navigation button:hover,
.tab-navigation button.active {
  color: white;
  border-bottom-color: white;
  background: rgba(255,255,255,0.1);
}

.app-main {
  background: white;
  min-height: calc(100vh - 140px);
  border-radius: 20px 20px 0 0;
  margin-top: 20px;
  overflow: hidden;
}

.current-session {
  position: fixed;
  bottom: 20px;
  left: 20px;
  background: rgba(0,0,0,0.8);
  color: white;
}